<template>
  <div>
    <el-table
      class="eltable"
      :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
      style="width: 90%"
    >
      <el-table-column label="姓名" prop="name"></el-table-column>
      <el-table-column label="性别" prop="gender"></el-table-column>
      <el-table-column label="年龄" prop="age"></el-table-column>
      <el-table-column label="地址" prop="address"></el-table-column>
      <el-table-column label="电话" prop="phone"></el-table-column>
      <el-table-column label="email" prop="email"></el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="scope">
          <!-- <input class="buginput" type="text" v-model="search" placeholder="输入关键字搜索" /> -->
          <el-input  size="mini" placeholder="输入关键字搜索" v-model="search"  @input="change($event)"/>
          
        </template>
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="updateCustomer(scope.$index, scope.row)">修改</el-button>
          <el-button size="mini" type="success" @click="removeCustomer(scope.$index, scope.row)">删除</el-button>

          <el-dialog title :visible.sync="dialogFormVisible">
            <el-form :model="form" class="elform">
              <el-form-item label="患者姓名" :label-width="formLabelWidth">
                <el-input class="two" v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="出生日期" :label-width="formLabelWidth">
                <!-- <el-input class="two" v-model="form.age" autocomplete="off"></el-input> -->
                <template>
                  <div class="radio">
                    <span class="demonstration"></span>
                    <el-date-picker
                      v-model="form.birthday"
                      type="date"
                      placeholder="选择日期">
                    </el-date-picker>
                  </div>
                </template>
              </el-form-item>
              <el-form-item label="患者性别" :label-width="formLabelWidth">
                <template>
                  <div class="radio">
                    <el-radio v-model="form.gender" label="男">男</el-radio>
                    <el-radio v-model="form.gender" label="女">女</el-radio>
                  </div>
                </template>
              </el-form-item>
              <el-form-item label="患者地址" :label-width="formLabelWidth">
                <!-- <el-input class="two" v-model="form.address" autocomplete="off"></el-input> -->
                <div class="two1">
  <span class="demonstration"></span>
  <el-cascader
    v-model="value"
    :options="options"
    :props="{ expandTrigger: 'hover' }"
    @change="handleChange"></el-cascader>
</div>
              </el-form-item>
              <el-form-item label="患者电话" :label-width="formLabelWidth">
                <el-input class="two" v-model="form.phone" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="患者电邮" :label-width="formLabelWidth">
                <el-input class="two" v-model="form.email" autocomplete="off"></el-input>
              </el-form-item>
              
            </el-form>
            <div slot="footer" class="dialog-footer">
              <!-- <el-button @click="selectCancel">取 消</el-button> -->
              <el-button type="success" @click="selectSure">确 定</el-button>
            </div>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>



<script>
import { getAllCuer,updateCusInformation,getCity } from "../../network/data";
export default {
  props: ["cuser"],
  data() {
    return {
      value:[],
      options:[],
      radio: "",
      input: "",
      dialogFormVisible: false,
      form: {
        name: "",
        birthday: "",
        gender: "",
        address: "",
        phone: "",
        email: ""
      },
      formLabelWidth: "120px",
      tableData: [],
      search: ""
    };
  },
  created() {
    this.getAllCuer()
    getCity().then(res=>{
      this.options=res
      
      
    })
  },
  methods: {
    handleChange (value) {
      console.log(value);
      
    },
    updateCustomer(index, row) {
      this.form._id = row._id
      this.form.insertDate = row.insertDate
      this.form.name = row.name
      this.form.gender = row.gender
      this.form.birthday = row.birthday
      this.form.address = row.address
      this.form.phone = row.phone
      this.form.email = row.email
      this.form.password=row.password
      this.dialogFormVisible = true
    },
    removeCustomer(index, row) {},
    selectSure() {
   
      if(this.value.length!=0)//填了地址。else未填地址
      this.form.address=this.value.reduce((a,b,c)=>{return a+"-"+b})
      else
      this.form.address=''
      updateCusInformation(this.form).then(res=>{
        console.log(res);
        
        this.getAllCuer()
      })
      this.dialogFormVisible = false
      
      
    },
    remoteMethod() {},
    getAllCuer() {
      getAllCuer().then(res => {
        this.tableData = []
        for (let i of res){
          i.age=this.jsGetAge(i.birthday)
          this.tableData.push(i)
        }
      });
    },
 
    jsGetAge(birthday){
      birthday=new Date(birthday)
      var d=new Date()
      var age = d.getFullYear()-birthday.getFullYear()-((d.getMonth()<birthday.getMonth()
      || d.getMonth()==birthday.getMonth() && d.getDate()<birthday.getDate())?1:0)
      return age
    }
  }
};
</script>
<style>
.elselct {
  position: absolute;
  left: 0;
}
.eltable {
  position: absolute;
  top: 10%;
  margin-left: 1%;
}
.two1 {
  position: absolute;
  left: 0;
  width: 35%
}
.two {
  position: absolute;
  left: 0;
  width: 30%;
}
.elpopover {
  margin: 0;
  padding: 0;
}
.buginput {
  width: 120px;
  height: 20px;
}
.radio {
  position: absolute;
  left: 0;
  
}
</style>